import React from "react";
import { Spring, config } from "react-spring/renderprops";
const HeroSection = () => {
  return (
    <Spring
      from={{
        opacity: 0,
      }}
      to={{
        opacity: 1,
      }}
      config={config.molasses}
    >
      {(props) => <div style={props}>HeroSection</div>}
    </Spring>
  );
};

export default HeroSection;
