import React from 'react';
import emailjs from 'emailjs-com';

import './message-form.styles.scss';
import FormInput from '../form-input/form-input.component';
import CustomButton from '../custom-button/custom-button.component';

import messageMe from '../../assets/messageMe.svg'
class MessageForm extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            user_name: '',
            user_email: '',
            message: ''
        }
    }

    handleChange = event => {
        const { name, value } = event.target;
        this.setState({
            [name]: value
        });
    }

    handleSubmit = event => {
        event.preventDefault();

        emailjs
        .sendForm('default_service', 'svg_portfolio', event.target, 'user_WRuUapGwUf7srVPu7kfAa')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });

        const customButton = event.target.querySelector('button');
        customButton.style.animationPlayState = 'running';
        customButton.addEventListener('animationend',()=>{
            customButton.parentElement.style.display = 'none'; 
            const div = document.createElement('div');
            div.className = 'thankYouForMessaging';
            div.innerHTML = "Thank You For Messaging";
            document.querySelector('.contact').append(div);
        });
        
        this.setState({
            user_name: '',
            user_email: '',
            message: ''
        });
    }


    render(){
        return(
            <form action='/' method='post' className='message-form' onSubmit={this.handleSubmit}>
                <h2> <img src={messageMe} alt=''/></h2>
                
                <FormInput 
                    type='text' 
                    name='user_name' 
                    label='Name'
                    handleChange={this.handleChange}
                    value={this.state.user_name}
                    required
                />
                <FormInput 
                    type='email' 
                    name='user_email' 
                    label='Email'
                    onChange={this.handleChange} 
                    value={this.state.user_email}
                    required
                />
                <FormInput 
                    type='textarea' 
                    name='message' 
                    label='Message'
                    onChange={this.handleChange} 
                    value={this.state.message}
                    required
                />
                <CustomButton type='submit' >
                    Send
                </CustomButton>
            </form>
        );
    }
}

export default MessageForm;