import React, { useState, useCallback } from "react";
import { useTransition, animated } from "react-spring";

import MessageMe from "../message-me/message-me.component";
import MessageForm from "../message-form/message-form.component";

import "./contact-slide.styles.scss";

const pages = [
  ({ style }) => (
    <animated.div className="contact" style={{ ...style }}>
      <MessageMe />
    </animated.div>
  ),
  ({ style }) => (
    <animated.div className="contact" style={{ ...style }}>
      <MessageForm />
    </animated.div>
  ),
];
const ContactSlide = () => {
  const [index, set] = useState(0);
  const onClick = useCallback(
    () => set((state) => (state === 0 ? state + 1 : state)),
    []
  );
  const transitions = useTransition(index, (p) => p, {
    from: { opacity: 0, transform: "translate3d(100%,0,0)" },
    enter: { opacity: 1, transform: "translate3d(0%,0,0)" },
    leave: { opacity: 0, transform: "translate3d(-50%,0,0)" },
  });
  return (
    <div className="contact-slide" onClick={onClick}>
      {transitions.map(({ item, props, key }) => {
        const Page = pages[item];
        return <Page key={key} style={props} />;
      })}
    </div>
  );
};

export default ContactSlide;
