import React from "react";

import "./project.styles.scss";
import SVG from "../svg/svg.component";
const Project = ({
  projectName,
  projectTech,
  href,
  projectImage,
  projectDetailsBackground,
  projectInfo,
}) => {
  const backgroundStyle = {
    backgroundColor: projectDetailsBackground,
  };
  const projectBorderStyle = {
    border: `5px solid ${projectDetailsBackground}`,
  };
  return (
    <div className="project-card" style={projectBorderStyle}>
      <div className="project-svg">
        <SVG item={projectImage} />
      </div>
      <div className="project-details" style={backgroundStyle}>
        <h1>{projectName}</h1>
        <h5>{projectTech}</h5>
        <p>{projectInfo}</p>
        <a href={href} rel="noopener noreferrer" target="_blank">
          Go to Site
        </a>
      </div>
    </div>
  );
};
export default Project;
