import React, { useState, useCallback } from "react";
import { useTransition, animated } from "react-spring";
import Project from "../project/project.component";

import "./projects.styles.scss";

const pages = [
  ({ style }) => (
    <animated.div className="project" style={{ ...style }}>
      <Project
        projectName="Todo For Teams"
        projectTech="MERN Stack"
        href="https://todo-for-teams.herokuapp.com/"
        projectImage="todo"
        projectDetailsBackground="#6c63ff"
        projectInfo="Most simple todo web app for a team"
      />
    </animated.div>
  ),
  ({ style }) => (
    <animated.div className="project" style={{ ...style }}>
      <Project
        projectName="Chit Chat"
        projectTech="React Firebase"
        href="https://chitchat-reactjs.herokuapp.com/"
        projectImage="chatting"
        projectDetailsBackground="#c4c1ff"
        projectInfo="Group chat web app"
      />
    </animated.div>
  ),
  ({ style }) => (
    <animated.div className="project" style={{ ...style }}>
      <Project
        projectName="Crown Clothing"
        projectTech="React Redux Firebase etc"
        href="https://crown-xx-clothing.herokuapp.com/"
        projectImage="crown"
        projectDetailsBackground="#3f3d56"
        projectInfo="Ecommerce website"
      />
    </animated.div>
  ),
];

const Projects = () => {
  const [index, set] = useState(0);
  const onClick = useCallback(() => set((state) => (state + 1) % 3), []);
  const transitions = useTransition(index, (p) => p, {
    from: { opacity: 0, transform: "translate3d(100%,0,0)" },
    enter: { opacity: 1, transform: "translate3d(0%,0,0)" },
    leave: { opacity: 0, transform: "translate3d(-50%,0,0)" },
  });
  return (
    <div className="projects" onClick={onClick}>
      {transitions.map(({ item, props, key }) => {
        const Page = pages[item];
        return <Page key={key} style={props} />;
      })}
    </div>
  );
};

export default Projects;
